<template>
    <div class="container mt-3">
        <div class="row d-flex">
            <div class="d-flex justify-content-between"> 
            <h1>Búsqueda:</h1>
            <input type="text" placeholder="Buscar por código" v-model="input" class="ms-auto me-1">

            </div>
            <div v-if="resultadoBusqueda != undefined" class="my-5 d-flex justify-content-center">
                <div class="card rounded" style="width: 18rem;">
                    <img :src="resultadoBusqueda.img" class="card-img-top" alt="...">
                    <div class="card-body">
                        <h5 class="card-title">{{resultadoBusqueda.nombre}}</h5>
                        <div class="d-flex justify-content-between my-3">
                            <h6 class="card-subtitle mb-2 text-black-50">Digital key <i class="fas fa-key"></i></h6>
                            <h5 class="card-text text-success text-end">${{resultadoBusqueda.precio}}<small> clp</small></h5>
                        </div>
                        <div class="d-flex justify-content-end mt-3">
                            <button type="button" class="btn btn-success end-1" @click="vender(resultadoBusqueda.codigo), agregarVenta(resultadoBusqueda), totalVentas(resultadoBusqueda)">
                                Comprar <i class="fas fa-plus"></i>
                            </button>
                        </div>
                        
                    </div>
                </div>
            </div>
        </div>
        <div class="d-flex justify-content-center">
            <h2 class="text-danger">¡Tenemos en total {{totalStock}} unidades en stock!</h2>
        </div>
        <div class="mb-5 d-flex justify-content-center">
            <h5 class="text-muted">Entre {{totalJuegos}} juegos distintos</h5>
        </div>

        <div>
            <router-view class="my-5 d-flex justify-content-center"> </router-view>
        </div>
    </div>
</template>

<script>
import {mapGetters, mapActions, mapMutations} from 'vuex'
export default {
    computed:{
        ...mapGetters(['filtrarPorCodigo', 'cantidadDeJuegos', 'cantidadDeStock']),
        resultadoBusqueda(){
            return this.$store.getters.filtrarPorCodigo(this.input)
        },
        totalJuegos(){
            return this.$store.getters.cantidadDeJuegos
        },
        totalStock(){
            return this.$store.getters.cantidadDeStock
        },
        
    },
    data(){
        return{
            input:'',
        }
    },
    methods:{
        ...mapMutations(['restarPorCodigo']),
        ...mapActions(['agregarVenta', 'totalVentas', 'actualizarStock', 'venderProducto']),
        nuevoStock(){
            this.actualizarStock()
        },
        vender(j){
            this.restarPorCodigo(j)
        },
    },


}
</script>

<style scoped>
input{
    max-width: 15%;
}

.card{
    box-shadow: rgba(0, 0, 0, 0.308) 0px 0px 10px;
}
</style>